.UserHeader {
  position: absolute;
  top: 0;
  right: 0;
  height: 60px;
  width: 100%;
}

.UserHeader p {
  display: block;
  float: right;
  line-height: 60px;
  margin: 0 1rem;
}

.UserHeader .settings {
  cursor: pointer;
  float: right;
  height: 60px;
  line-height: 56px;
  padding: 0 1rem;
  font-size: 2em;
  border-left: 1px solid #fff;
  color: #fff;
  background: #335;
  background: -webkit-gradient(linear, left top, left bottom, from(#222), to(#335));
  background: -webkit-linear-gradient(top, #222 0%, #335 100%);
  background: -o-linear-gradient(top, #222 0%, #335 100%);
  background: linear-gradient(to bottom, #222 0%, #335 100%);
}

.UserHeader .settings:hover,
.UserHeader .settings:active {
  color: #99f;
}

.UserHeader .logoutButton {
  cursor: pointer;
  display: block;
  float: right;
  height: 60px;
  border: none;
  border-left: 1px solid #fff;
  color: #fff;
  background: #335;
  background: -webkit-gradient(linear, left top, left bottom, from(#222), to(#335));
  background: -webkit-linear-gradient(top, #222 0%, #335 100%);
  background: -o-linear-gradient(top, #222 0%, #335 100%);
  background: linear-gradient(to bottom, #222 0%, #335 100%);
}

.UserHeader .logoutButton:hover,
.UserHeader .logoutButton:active {
  background: #d22;
  background: -webkit-gradient(linear, left top, left bottom, from(#d22), to(#a22));
  background: -webkit-linear-gradient(top, #d22 0%, #a22 100%);
  background: -o-linear-gradient(top, #d22 0%, #a22 100%);
  background: linear-gradient(to bottom, #d22 0%, #a22 100%);
}




