.NewTaskForm {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #999;
  padding-left: 40px;
  background: #112;
  background: -webkit-gradient(linear, left top, left bottom, from(#112), to(#113));
  background: -webkit-linear-gradient(top, #112 0%, #113 100%);
  background: -o-linear-gradient(top, #112 0%, #113 100%);
  background: linear-gradient(to bottom, #112 0%, #113 100%);
  position: relative;
}

.NewTaskForm.isOver {
  border-top: 3px solid #aaf;
}

.NewTaskForm .addTaskIcon {
  color: #fff;
  height: 24px;
  width: 24px;
  position: absolute;
  top: 8px;
  left: 8px;
}

.NewTaskForm input {
  background-color: transparent;
  border: none;
  color: #666;
  width: 100%;
}

.NewTaskForm input:focus {
  border: none;
  color: #fff;
  outline: none;
}

.NewTaskForm input::-webkit-input-placeholder {
  color: #666;
}

.NewTaskForm input:-ms-input-placeholder {
  color: #666;
}

.NewTaskForm input::placeholder {
  color: #666;
}

.NewTaskForm input:focus::-webkit-input-placeholder {
  color: #999;
}

.NewTaskForm input:focus:-ms-input-placeholder {
  color: #999;
}

.NewTaskForm input:focus::placeholder {
  color: #999;
}



